<template>
  <div class="switcher">
    <label
      v-for="item of props.value"
      :key="item.id"
      class="switcher__item"
      :class="{ selected: item.isSwitch === true }"
      @click="selectItem(item.id)"
    >
      {{ item.value }}
    </label>
  </div>
</template>

<script setup>
import { defineProps, defineEmits } from "vue";

//props type Object
// id: number,
// value: string,
// isSwitch: boolean,

const props = defineProps({
  value: Array,
});

const emit = defineEmits(["updateValue"]);

const selectItem = (id) => {
  const updatedValue = props.value.map((item) => ({
    ...item,
    isSwitch: item.id === id,
  }));

  emit("updateValue", updatedValue);
};
</script>

<style lang="scss">
@import url(../../styles/variable.css);
@import url(../../styles/fonts.css);
.switcher {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  &__item {
    width: calc(50% - 10px);
    background: var(--yellow);
    color: var(--text-black);
    padding: 5px;
    font-size: 20px;
    font-family: "Alumni-500";
    border-radius: 5px;
    border: 1px solid var(--white);
    text-align: center;
    &.selected {
      color: var(--yellow);
      background: var(--dark-gray);
      border: 1px solid var(--white);
      transition: all 0.2s ease;
    }
  }
}
</style>
