<template>
  <div class="title" v-html="checkTitleInfo"></div>
</template>

<script setup>
import { defineProps, computed } from "vue";

const props = defineProps({
  titleParams: Object,
});

const checkTitleInfo = computed(() => {
  if (props.titleParams) {
    return `<${props.titleParams.tag} style='font-size: ${props.titleParams.size}${props.titleParams.dimension};' class='title-${props.titleParams.tag}'>${props.titleParams.title}</${props.titleParams.tag}>`;
  }
});
</script>
<style lang="scss"></style>
