<template>
  <input
    class="input__text"
    type="text"
    v-model="textValue"
    :placeholder="getPlaceholder"
    v-focus-class
    @input="setNewValue"
    @focus="setNewValue"
    :disabled="props.disabled"
  />
</template>

<script setup>
import { defineProps, computed, ref, defineEmits } from "vue";
import { vFocusClass } from "@/directives/FocusDirective";

//props textParams
// value: string,
// isValid?: Ref<boolean | null>,
// setQueryParams?: (type: string, value:  string) => void,
// type?: string,
// placeholder?: string,

const props = defineProps({
  textParams: {
    type: Object,
    required: true,
  },
  propertyName: {
    type: String,
    required: false,
  },
  disabled: {
    type: Boolean,
    required: false,
  },
});

const emit = defineEmits(["input"]);

const getPlaceholder = computed(() => {
  if (props.textParams) {
    return props.textParams.placeholder;
  } else {
    return "Текст";
  }
});

const textValue = ref("");

const setNewValue = () => {
  emit("input", {
    eventValue: textValue.value,
    eventType: props.textParams.type,
    eventName: props.propertyName,
    eventValid: textValue.value.length > 0 ? true : false,
  });
};

if (props.textParams.value) {
  textValue.value = props.textParams.value;
}
</script>

<style scoped lang="scss">
@import url(../../styles/variable.css);

input[type="text"] {
  caret-color: var(--yellow);
}
</style>
